import React, {Component} from 'react';
import {
    Button,
    Checkbox,
    Div,
    File,
    FormLayout,
    FormStatus,
    Group,
    Header,
    Input,
    Link,
    Panel,
    PanelHeader,
    Radio,
    Spinner,
    Textarea
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import './Home.css';
import bridge from '@vkontakte/vk-bridge';
import MeetBox from '../components/MeetBox/MeetBox';
import {getMessage} from '../js/helpers';
import Icon28CameraOutline from '@vkontakte/icons/dist/28/camera_outline';

class AddMeetPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: false,
            token: false,
            error: false,
            message: false
        }
        this.onChange = this.onChange.bind(this);
        this.AddMeet = this.AddMeet.bind(this);
    };

    AddMeet = async () => {
        const {api, setParentState, openDoneSnackbar, openErrorSnackbar, state} = this.props;
        setParentState({disabled: true});
        const {success, failed} = await api.AddMeet({
            isGroup: state.groupSelected,
            name: state.name,
            start: state.start,
            finish: state.finish,
            description: state.description,
            photo: state.photo
        });
        if (success) {
            const mes = `–í–∞—à–∞ –ø–µ—Ç–∏—Ü–∏—è "${state.name}>" –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∞ –Ω–∞ –º–æ–¥–µ—Ä–∞—Ü–∏—é.`;
            if (state.noty) {
                fetch('https://cors-anywhere.herokuapp.com/https://groovy-apricot.glitch.me/', {method: 'GET'})
                    .then(res => bridge.send("VKWebAppSendPayload", {"group_id": 189366357, "payload": {"mes": mes}}));
            }
            setParentState({
                activePanel: 'meets',
                activeStory: 'home',
                symbols_name: '',
                symbols_description: '',
                name: '',
                description: '',
                photo: false,
                accept: false
            });
            openDoneSnackbar(success);
        } else if (failed) {
            openErrorSnackbar(failed)
        } else {
            openErrorSnackbar('–°–æ–∑–¥–∞–≤–∞—Ç—å –ø–µ—Ç–∏—Ü–∏–∏ –º–æ–∂–Ω–æ –Ω–µ —á–∞—â–µ —á–µ—Ç—ã—Ä—ë—Ö —Ä–∞–∑ –≤ –¥–µ–Ω—å')
        }
        setTimeout(() => setParentState({
            popout: null
        }), 3000)  // –ø–æ–Ω—è–ª, —á—Ç–æ –≤—Å—ë —Ç–∞–∫–∏ –Ω—É–∂–Ω–æ
    }

    onChange = async (e) => {
        const {name, value} = e.currentTarget;
        if (name === 'file') {
            const file = document.getElementById('file').files[0];
            if (!file) {
                this.setState({error: true})
                this.setState({message: '–§–∞–π–ª –Ω–µ –±—ã–ª –≤—ã–±—Ä–∞–Ω'})
                return
            }
            if (!(file.type.match('image/*'))) {
                this.setState({error: true})
                this.setState({message: '–§–æ—Ä–º–∞—Ç –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —Ñ–∞–π–ª–∞ –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è.'})
                return
            }
            if (file.size > 16777216) {
                this.setState({error: true})
                this.setState({message: '–†–∞–∑–º–µ—Ä –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —Ñ–∞–π–ª–∞ —Å–ª–∏—à–∫–æ–º –±–æ–ª—å—à–æ–π.'})
                return
            }

            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = (e) => save(e);

            const save = e => {
                if (!e.target.result) return;
                console.log(e.target.result);
                this.props.setParentState({
                    photo: e.target.result
                });
                this.setState({
                    error: false,
                    message: '–û–±–ª–æ–∂–∫–∞ –∑–∞–≥—Ä—É–∂–µ–Ω–∞.',
                    disabled: false
                });
            }
        } else {
            this.props.setParentState({[name]: value});
            if (name === 'name') this.props.setParentState({symbols_name: `${value.length}/60`})
            if (name === 'description') this.props.setParentState({symbols_description: `${value.length}/254`})
        }
    };

    checkIfAllOk(name, desc, start, end, photo, approve) {
        if ((name === undefined || name === '')
            || (desc === undefined || desc === '')
            || (start === undefined || start === '' || !start)
            || ((Date.parse(start) < Date.parse(new Date())) && (new Date(start).getDay() < new Date().getDay()))
            || (end === undefined || end === '' || !end)
            || ((Date.parse(end) < Date.parse(new Date())) && (new Date(end).getDay() < new Date().getDay()))
            || Date.parse(start) > Date.parse(end)
            || !photo || !approve
        ) {
            return false
        } else {
            return true
        }
    }

    render() {
        const onChange = this.onChange;
        const {id, state, setParentState} = this.props;
        const {
            name, start, snackbar, finish, description, symbols_name, symbols_description, photo, accept, attemp
        } = state;
        const formLang = getMessage('forms');
        const meet = {
            name: name || '–°—ã—Ä–∫–∏ –¥–æ—Ä–æ–≥–∏–µ!',
            photo: photo || 'https://img.tyt.by/n/03/8/glazirovannyy_syrok_1.jpg',
            owner_name: state.fetchedUser.first_name,
            owner_surname: state.fetchedUser.last_name,
            members_amount: 10,
            ismember: true
        }
        return (
            <Panel id={id}>
                <PanelHeader>{getMessage('add_meet_page')}</PanelHeader>
                <Group header={<Header mode="secondary">–ü—Ä–µ–¥–ø—Ä–æ—Å–º–æ—Ç—Ä</Header>}>
                    <Div>
                        <MeetBox
                            meet={meet}
                            setParentState={null}
                            style={{marginLeft: 12, marginRight: 12}}
                        />
                    </Div>
                </Group>
                <Group header={<Header mode="secondary">–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –ø–µ—Ç–∏—Ü–∏–∏</Header>}>
                    <FormLayout>
                        {state.isCurrentGroupAdmin && state.currentGroupInfo &&
                        <Group title='–ê–≤—Ç–æ—Ä'>
                            <Radio
                                name="radio"
                                value="user"
                                onChange={(e) => setParentState({
                                    groupSelected: false
                                })}
                                checked={!state.groupSelected}
                            >{state.fetchedUser.first_name} {state.fetchedUser.last_name}
                            </Radio>
                            <Radio
                                name="radio"
                                value="group"
                                checked={state.groupSelected}
                                onChange={(e) => setParentState({
                                    groupSelected: e.currentTarget.checked
                                })}
                                description="–ü–µ—Ç–∏—Ü–∏—è –±—É–¥–µ—Ç –æ–ø—É–±–ª–∏–∫–æ–≤–∞–Ω–∞ –æ—Ç –∏–º–µ–Ω–∏ —ç—Ç–æ–≥–æ —Å–æ–æ–±—â–µ—Å—Ç–≤–∞">
                                {state.currentGroupInfo.name}
                            </Radio>
                        </Group>
                        }
                        <Input
                            type="text"
                            top={<div style={{marginTop: -12}}>–ù–∞–∑–≤–∞–Ω–∏–µ {symbols_name}</div>}
                            name="name"
                            maxLength='60'
                            value={name}
                            placeholder={'–°—ã—Ä–∫–∏ –¥–æ—Ä–æ–≥–∏–µ!'}
                            onChange={onChange}
                            status={
                                (name === undefined || name === '') && attemp ? 'error' : 'valid'
                            }
                            bottom={
                                (name === undefined || name === '') && attemp ? '–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, —É–∫–∞–∂–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –ø–µ—Ç–∏—Ü–∏–∏' : ''
                            }
                        />
                        <Textarea
                            top={<div style={{marginTop: -12}}>–û–ø–∏—Å–∞–Ω–∏–µ {symbols_description}</div>}
                            maxLength='254'
                            name="description"
                            placeholder="–¢—Ä–µ–±—É–µ–º –ø–æ–Ω–∏–∑–∏—Ç—å —Ü–µ–Ω—É –Ω–∞ —Å—ã—Ä–∫–∏"
                            value={description}
                            onChange={onChange}
                            status={
                                (description === undefined || description === '') && attemp ? 'error' : 'valid'
                            }
                            bottom={(description === undefined || description === '') && attemp ? '–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, —É–∫–∞–∂–∏—Ç–µ –æ–ø–∏—Å–∞–Ω–∏–µ –ø–µ—Ç–∏—Ü–∏–∏' : ''}
                        />
                        <Input
                            type="date"
                            top={<div style={{marginTop: -12}}>{formLang.start}</div>}
                            name="start"
                            value={start}
                            onChange={onChange}
                            status={start === undefined || start === '' || !start ? 'error' : (Date.parse(start) < Date.parse(new Date())) && (new Date(start).getDay() < new Date().getDay()) ? 'error' : 'valid'}
                            bottom={start === undefined || start === '' || !start ? '–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, —É–∫–∞–∂–∏—Ç–µ –¥–∞—Ç—É –Ω–∞—á–∞–ª–∞ –ø–µ—Ç–∏—Ü–∏–∏' : (Date.parse(start) < Date.parse(new Date())) && (new Date(start).getDay() < new Date().getDay()) ? '–î–∞—Ç–∞ –Ω–∞—á–∞–ª–∞ –ø–µ—Ç–∏—Ü–∏–∏ —É–∂–µ –ø—Ä–æ—à–ª–∞' : ''}
                        />
                        <Input
                            type="date"
                            top={<div style={{marginTop: -12}}>{formLang.finish}</div>}
                            name="finish"
                            value={finish}
                            onChange={onChange}
                            status={finish === undefined || finish === '' || !finish ? 'error' : (Date.parse(finish) < Date.parse(new Date())) && (new Date(finish).getDay() < new Date().getDay()) ? 'error' : Date.parse(start) > Date.parse(finish) ? 'error' : 'valid'}
                            bottom={finish === undefined || finish === '' || !finish ? '–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, —É–∫–∞–∂–∏—Ç–µ –¥–∞—Ç—É –æ–∫–æ–Ω—á–∞–Ω–∏—è –ø–µ—Ç–∏—Ü–∏–∏' : (Date.parse(finish) < Date.parse(new Date())) && (new Date(finish).getDay() < new Date().getDay()) ? '–î–∞—Ç–∞ –æ–∫–æ–Ω—á–∞–Ω–∏—è –ø–µ—Ç–∏—Ü–∏–∏ —É–∂–µ –ø—Ä–æ—à–ª–∞' : Date.parse(start) > Date.parse(finish) ? '–î–∞—Ç–∞ –æ–∫–æ–Ω—á–∞–Ω–∏—è –ø–µ—Ç–∏—Ü–∏–∏ —Ä–∞–Ω—å—à–µ –¥–∞—Ç—ã –Ω–∞—á–∞–ª–∞' : ''}
                        />
                        <File
                            id='file'
                            maxfiles={1}
                            acceptfiletypes={['image/png', 'image/jpg', 'image/jpeg', '.png']}
                            minfilesize={0}
                            top={<div style={{marginTop: -12}}>{formLang.photo}</div>}
                            type="file"
                            before={<Icon28CameraOutline width={24} height={24}/>}
                            size="l"
                            mode="outline"
                            name="file"
                            onChange={onChange}
                            status={!photo && attemp ? 'error' : 'valid'}
                            bottom={!photo && attemp ? '–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –∑–∞–≥—Ä—É–∑–∏—Ç–µ –æ–±–ª–æ–∂–∫—É –ø–µ—Ç–∏—Ü–∏–∏' : ''}
                        />
                        {this.state.error &&
                        <div style={{marginTop: -12}}>
                            <FormStatus header="–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –æ–±–ª–æ–∂–∫–∏" mode="error">
                                {this.state.message}
                            </FormStatus>
                        </div>
                        }
                        {photo && !this.state.error &&
                        <div style={{marginTop: -12}}>
                            <FormStatus header="–û–±–ª–æ–∂–∫–∞ —É—Å–ø–µ—à–Ω–æ –∑–∞–≥—Ä—É–∂–µ–Ω–∞" mode="default">
                                –¢–µ–ø–µ—Ä—å, –µ–µ –º–æ–∂–Ω–æ —É–≤–∏–¥–µ—Ç—å –≤ –ø—Ä–µ–¥–ø—Ä–æ—Å–º–æ—Ç—Ä–µ
                            </FormStatus>
                        </div>
                        }
                        <Checkbox
                            style={{marginTop: -12}}
                            checked={state.accept}
                            onChange={(e) => this.props.setParentState({accept: e.currentTarget.checked})}
                            status={!accept && attemp ? 'error' : 'valid'}
                            bottom={!accept && attemp ? '–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø—Ä–∏–º–∏—Ç–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–æ–µ —Å–æ–≥–ª–∞—à–µ–Ω–∏–µ' : ''}
                        >–°–æ–≥–ª–∞—Å–µ–Ω —Å
                            <Link target="_blank"
                                  href="https://vk.com/@virtualmeetingsclub-pravila-razmescheniya"> –ø—Ä–∞–≤–∏–ª–∞–º–∏</Link></Checkbox>
                        <Checkbox style={{marginTop: -24}} disabled={state.noty} checked={state.noty} onChange={(e) => {
                            if (e.currentTarget.checked) {
                                console.log('–æ—Ç–ø—Ä–∞–≤–∏–ª–∏!')
                                bridge.send("VKWebAppAllowMessagesFromGroup", {"group_id": 189366357});
                            }
                        }}>
                            –£–≤–µ–¥–æ–º–∏—Ç—å –æ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–µ –º–æ–¥–µ—Ä–∞—Ü–∏–∏
                        </Checkbox>
                        <Button
                            mode="outline"
                            size="xl"
                            disabled={!this.checkIfAllOk(name, description, start, finish, photo, accept) && attemp}
                            style={{marginTop: -12}} onClick={() => {
                                setParentState({
                                    attemp: attemp + 1
                                })
                                if(!this.checkIfAllOk(name, description, start, finish, photo, accept)){
                                    return;
                                }
                            this.AddMeet();
                        }}>
                            {this.state.disabled ? <Spinner/> : formLang.add}
                        </Button>
                    </FormLayout>
                </Group>
                {snackbar}
            </Panel>
        );
    }
}

export default AddMeetPage;